<template>
	<tm-fullView>
		<template v-slot:default="{ info }">
			<view class="grey text " :class="$tm.vx.state().tmVuetify.black ? 'grey-darken-6 bk' : ''" :style="{ minHeight: info.height + 'px' }">
				<tm-menubars title="反馈bug或者意见" :showback="false"></tm-menubars>
				<tm-form @submit="subit">
					<tm-sheet :shadow="24" :round="10" :margin="[32, 40]">
						<tm-input
							v-model="req.email"
							:border-bottom="false"
							bg-color="grey-lighten-5"
							name="email"
							:padding="[0, 12]"
							required
							title="联系邮箱"
							:vertical="true"
							input-type="text"
						></tm-input>
						<view style="height: 24rpx;"></view>
						<tm-input
							v-model="req.word"
							:border-bottom="false"
							bg-color="grey-lighten-5"
							name="word"
							:padding="[0, 12]"
							required
							title="提交事项原由"
							:height="150"
							:vertical="true"
							input-type="textarea"
							:maxlength="215"
						></tm-input>
						<view class="py-32"><tm-button :round="24" theme="bg-gradient-primary-lighten" navtie-type="form" dense block>提交反馈</tm-button></view>
					</tm-sheet>
				</tm-form>
				<view style="height: 32rpx;"></view>
				<view class="flex-center "><tm-images :width="60" src="static/logo_great.png"></tm-images></view>
				<view class="py-24 px-0 text-size-xs text-grey-lighten-1 text mx-32 text-align-center">
					本UI经过细致打磨，并且还在不断的细化中，让它成为高效率、高颜值、高覆盖率的组件库，需要各方努力贡献。
					<view>请不要盗版，可免费使用。</view>
					<view class=" text-weight-b" :class="[`text-${$tm.vx.state().tmVuetify.color || 'primary'}`]">
						用户Q群：
						<text selectable>18593916</text>
					</view>
				</view>
				<tm-dialog v-model="show" title="反馈提醒" content="感谢你的提交,我会优先答复邮件反馈.也尽请加入Q群更好的反馈:18593916,加群答案:tm-vuetify"></tm-dialog>
				<!-- <tm-bottomnavigation  :list="$tm.vx.state().test.tabarlist"></tm-bottomnavigation> -->
			</view>
		</template>
	</tm-fullView>
</template>

<script>
import tmSheet from '@/tm-vuetify/components/tm-sheet/tm-sheet.vue';
import tmFullView from '@/tm-vuetify/components/tm-fullView/tm-fullView.vue';
import tmMenubars from '@/tm-vuetify/components/tm-menubars/tm-menubars.vue';
import tmInput from '@/tm-vuetify/components/tm-input/tm-input.vue';
import tmButton from '@/tm-vuetify/components/tm-button/tm-button.vue';
import tmForm from '@/tm-vuetify/components/tm-form/tm-form.vue';
import tmDialog from '@/tm-vuetify/components/tm-dialog/tm-dialog.vue';
import tmImages from '@/tm-vuetify/components/tm-images/tm-images.vue';
import tmBottomnavigation from '@/tm-vuetify/components/tm-bottomnavigation/tm-bottomnavigation.vue';

export default {
	components: { tmSheet, tmFullView, tmMenubars, tmInput, tmButton, tmForm, tmDialog, tmImages, tmBottomnavigation },

	data() {
		return {
			req: {
				email: '',
				word: ''
			},
			show: false
		};
	},
	methods: {

		async subit(e) {
			console.log(e);
			if (!e) return;
			if (!uni.$tm.test.email(e.email)) {
				uni.$tm.toast('请输入合法邮箱。');
				return;
			}
			if (uni.$tm.test.empty(e.word)) {
				uni.$tm.toast('内容不能为空');
				return;
			}

			let rulst = await this.$tm.request.post('', e).catch(e => {});

			if (rulst.code == 0) {
				this.req = { email: '', word: '' };
				this.show = true;
			} else {
				uni.$tm.toast(rulst.msg);
			}
		}
	}
};
</script>

<style lang="scss"></style>
